//Creo una clase que luego uso como etiqueta html
class Helloworld extends React.Component{
//Estado del componente
  state = {
    show: true
  }
  //Definimos funcion tipo flecha para cambiar estado
  cambiarEstado = () => {
    this.setState({show: !this.state.show})
  }
  render(){
    if(this.state.show){return(<div id="hello">
    <h3>{this.props.subtitle}</h3>
    {this.props.mytext}
    <button onClick={this.cambiarEstado}>Cambiar estado</button>
    </div>
    )}else{
      return <h1>
        No hay elementos
        <button onClick={this.cambiarEstado}>Cambiar estado</button>
        </h1>
    }
    
  }
}
